'use strict';

// MODULES //

import isFunction from '@stdlib/assert-is-function';
import isndarrayLike from '@stdlib/assert-is-ndarray-like';
import hasOwnProp from '@stdlib/assert-has-own-property';
import unaryReduceSubarrayBy from '@stdlib/ndarray-base-unary-reduce-subarray-by';
import ndims from '@stdlib/ndarray-ndims';
import base from '@stdlib/ndarray-base-find';
import getDType from '@stdlib/ndarray-dtype';
import getShape from '@stdlib/ndarray-shape';
import getOrder from '@stdlib/ndarray-base-order';
import maybeBroadcastArray from '@stdlib/ndarray-base-maybe-broadcast-array';
import broadcastScalar from '@stdlib/ndarray-base-broadcast-scalar';
import zeroTo from '@stdlib/array-base-zero-to';
import objectAssign from '@stdlib/object-assign';
import format from '@stdlib/error-tools-fmtprodmsg';
import DEFAULTS from './defaults.json';
import validate from './validate.js';
import getSentinelValue from './sentinel.js';


// MAIN //

/**
* Finds the first elements which pass a test implemented by a predicate function along one or more ndarray dimensions and assigns results to a provided output ndarray.
*
* @param {ndarray} x - input ndarray
* @param {ndarray} out - output ndarray
* @param {Options} [options] - function options
* @param {IntegerArray} [options.dims] - list of dimensions over which to perform a reduction
* @param {(*|ndarray)} [options.sentinelValue] - sentinel value
* @param {Function} predicate - predicate function
* @param {*} [thisArg] - predicate function execution context
* @throws {TypeError} first argument must be an ndarray-like object
* @throws {TypeError} second argument must be an ndarray-like object
* @throws {TypeError} options argument must be an object
* @throws {TypeError} predicate argument must be a function
* @throws {Error} must provide valid options
* @returns {ndarray} output ndarray
*
* @example
* import { isPrimitive as isEven } from '@stdlib/assert-is-even';
* import array from '@stdlib/ndarray-array';
* import empty from '@stdlib/ndarray-empty';
*
* // Create an input ndarray:
* var x = array( [ [ [ 1.0, 2.0 ], [ 3.0, 4.0 ] ], [ [ 0.0, 6.0 ], [ 7.0, 8.0 ] ] ] );
* // returns <ndarray>
*
* // Create an output ndarray:
* var y = empty( [], {
*     'dtype': x.dtype
* });
*
* // Perform reduction:
* var out = assign( x, y, isEven );
* // returns <ndarray>
*
* var bool = ( out === y );
* // returns true
*
* var v = y.get();
* // returns 2.0
*/
function assign( x, out, options, predicate, thisArg ) {
	var sentinelValue;
	var nargs;
	var opts;
	var err;
	var flg;
	var ctx;
	var sv;
	var cb;
	var dt;
	var N;
	var o;

	nargs = arguments.length;
	if ( !isndarrayLike( x ) ) {
		throw new TypeError( format( 'null5t', x ) );
	}
	if ( !isndarrayLike( out ) ) {
		throw new TypeError( format( 'invalid argument. Second argument must be an ndarray-like object. Value: `%s`.', out ) );
	}
	// Case: assign( x, out, predicate )
	if ( nargs < 4 ) {
		cb = options;
		if ( !isFunction( cb ) ) {
			throw new TypeError( format( 'null2b', cb ) );
		}
	}
	// Case: assign( x, out, options, predicate, thisArg )
	else if ( nargs > 4 ) {
		flg = true;
		o = options;
		cb = predicate;
		if ( !isFunction( cb ) ) {
			throw new TypeError( format( 'null2b', cb ) );
		}
		ctx = thisArg;
	}
	// Case: assign( x, out, predicate, thisArg )
	else if ( isFunction( options ) ) {
		cb = options;
		ctx = predicate;
	}
	// Case: assign( x, out, options, predicate )
	else if ( isFunction( predicate ) ) {
		flg = true;
		o = options;
		cb = predicate;
	}
	// Case: assign( x, out, ???, ??? )
	else {
		throw new TypeError( format( 'null2b', predicate ) );
	}
	dt = getDType( x );
	N = ndims( x );

	// Resolve function options:
	opts = objectAssign( {}, DEFAULTS );
	if ( flg ) {
		err = validate( opts, N, o );
		if ( err ) {
			throw err;
		}
	}
	// When a list of dimensions is not provided, reduce the entire input array across all dimensions...
	if ( opts.dims === null ) {
		opts.dims = zeroTo( N );
	}
	// Resolve the sentinel value to use when unable to find an element satisfying a provided predicate function...
	if ( hasOwnProp( opts, 'sentinelValue' ) ) {
		sentinelValue = opts.sentinelValue;
	} else {
		sentinelValue = getSentinelValue( dt );
	}
	// Broadcast sentinel value to match the output array shape...
	if ( isndarrayLike( sentinelValue ) ) {
		sv = maybeBroadcastArray( sentinelValue, getShape( out ) );
	} else {
		sv = broadcastScalar( sentinelValue, dt, getShape( out ), getOrder( out ) ); // eslint-disable-line max-len
	}
	// Perform the reduction:
	unaryReduceSubarrayBy( base, [ x, out, sv ], opts.dims, cb, ctx );
	return out;
}


// EXPORTS //

export default assign;
